<template>
    <div>
        <!-- <v-container fluid> -->
        <v-card elevation="0">
            <v-card-title> Payment </v-card-title>
            <v-simple-table>
                <tbody>
                    <tr v-for="(item, index) in cart" :key="`cart-${index}`">
                        <td>{{ item.data.name }}</td>
                        <td>{{ item.quantity }}</td>
                        <td>${{ item.data.amounts.primary * item.quantity }}</td>
                    </tr>
                    <tr>
                        <td>Tax</td>
                        <td></td>
                        <td>${{ tax }}</td>
                    </tr>
                    <tr>
                        <td class="font-weight-bold">Total</td>
                        <td></td>
                        <td class="font-weight-bold">${{ total }}</td>
                    </tr>
                </tbody>
            </v-simple-table>
        </v-card>
        <v-card v-if="Object.keys(addressInfo).length !== 0" elevation="0">
            <v-card-title> Address </v-card-title>
            <v-card-text>
                {{ addressInfo.street }}<br />
                {{ addressInfo.apt }}<br v-if="addressInfo.apt !== ''" />
                {{ addressInfo.city }}<span v-if="addressInfo.city !== ''">,</span> {{ addressInfo.state }}
                {{ addressInfo.zip }}<br />
                {{ addressInfo.country }}
            </v-card-text>
        </v-card>
        <v-card>
            <v-card-text>
                <v-checkbox
                    v-model="checkbox"
                    color="primary"
                    label="I agree to the Terms & Conditions, including the AutoPay requirement, and the Privacy Policy."
                ></v-checkbox>
            </v-card-text>
            <v-alert v-if="alert" type="error"> {{ alertMessage }}</v-alert>
            <v-card-actions>
                <v-btn
                    color="primary"
                    @click="submit"
                    :class="{ 'white--text': checkbox }"
                    :disabled="!checkbox"
                    :loading="loading"
                >
                    Submit
                </v-btn>
                <v-btn text> Cancel </v-btn>
            </v-card-actions>
            <!-- <v-alert v-if=alert type="error">test</v-alert> -->
        </v-card>
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    export default {
        name: 'OrderReview',
        data() {
            return {
                checkbox: false,
                loading: false,
                alert: false,
                alertMessage: '',

                cart: [],
            }
        },
        methods: {
            ...mapActions('user', [
                'userLogin',
                'addAccount',
                'addAddress',
                'createOrder',
                'addSubscriber',
                'userLogout',
            ]),
            ...mapActions('checkout', ['setAccountError', 'setNumberError', 'setStepper', 'resetModel']),
            async submit() {
                this.loading = true
                this.setAccountError(200)
                this.setNumberError(200)
                try {
                    if (sessionStorage.getItem('api_token') === null) {
                        await this.userLogin({
                            application_id: '05d8ab7fc9884950b6f90cad61dc88aa',
                            login_type: 1,
                            login_id: this.accountInfo.email,
                            login_secret: this.accountInfo.password,
                            device_id: 'device_id_1148',
                        })
                        await this.addAccount({
                            addAccount_target_id: sessionStorage.getItem('user_id'),
                            // addAccount_target_id: this.userID,
                            addAccount_target_type: 3,
                        })
                    }
                    await this.addAddress({
                        addAddress_parts: {
                            address: this.addressInfo.street,
                            city: this.addressInfo.city,
                            zip: this.addressInfo.zip,
                        },
                        addAddress_type: 1,
                        addAddress_target_id: sessionStorage.getItem('account_id'),
                        // addAddress_target_id: this.accountID,
                        addAddress_target_type: 2,
                    })

                    for await (let item of this.cart) {
                        for (let i = 0; i < item.quantity; i++) {
                            await this.createOrder({
                                entity_id: item.id,
                                entity_type: 'offer',
                                createOrder_target_id: sessionStorage.getItem('account_id'),
                                createOrder_target_type: 2,
                            })
                        }
                    }
                    // await this.addSubscriber({
                    //     addSubscriber_target_id: sessionStorage.getItem('account_id'),
                    //     // addSubscriber_target_id: this.accountID,
                    //     addSubscriber_target_type: 2,
                    //     subscriber_info: {
                    //         msisdn: this.numberInfo.number,
                    //     },
                    // })
                    await this.$router.push('/success')
                    // clear cart
                    // sessionStorage.removeItem('cart')
                    sessionStorage.setItem('cart', JSON.stringify([]))
                } catch (e) {
                    console.log(e)
                    // if the error is caused by the user login or creact account, go to the creact account step
                    if (e.request.responseURL.includes('login') || e.request.responseURL.includes('account')) {
                        // if error occured, logout user first
                        this.userLogout()
                        this.setAccountError(e.response.status)
                        // set step to account
                        this.setStepper(2)
                    } else if (e.request.responseURL.includes('/orderoe/create_order')) {
                        // set step to address
                        if (e.response.status === 500) {
                            this.alert = true
                            this.alertMessage = 'Internal Server Error'
                        } else {
                            this.setNumberError(e.response.status)
                            this.setStepper(4)
                        }
                    } else {
                        // other errors, show alert
                        this.alert = true
                        this.alertMessage = e.message
                    }
                    this.loading = false
                } finally {
                    this.loading = false
                }
            },
        },
        computed: {
            ...mapState('checkout', {
                planInfo: (state) => state.planInfo,
                accountInfo: (state) => state.accountInfo,
                addressInfo: (state) => state.addressInfo,
                paymentInfo: (state) => state.paymentInfo,
                numberInfo: (state) => state.numberInfo,
            }),
            ...mapState('user', {
                userID: (state) => state.user_id,
                apiToken: (state) => state.api_token,
                accountID: (state) => state.account_id,
            }),
            tax() {
                let subtotal = 0
                this.cart.forEach((item) => {
                    subtotal += Number(item.data.amounts.primary) * item.quantity
                })
                return subtotal * 0
            },
            total() {
                let subtotal = 0
                this.cart.forEach((item) => {
                    subtotal += Number(item.data.amounts.primary) * item.quantity
                })
                return (subtotal + this.tax).toFixed(2)
            },
        },
        mounted() {
            this.resetModel()
            this.cart = JSON.parse(sessionStorage.getItem('cart'))
        },
        beforeDestroy() {},
    }
</script>

<style scoped></style>
