// create vue router

import VueRouter from 'vue-router'
import RwHome from '@/pages/RwHome'
import RwCheckout from '@/pages/RwCheckout'
import RwSuccess from '@/pages/RwSuccess'
import RwDashboard from '@/pages/RwDashboard'
import RwLogin from '@/pages/RwLogin'
import RwTest from '@/pages/RwTest'
import RwCart from '@/pages/RwCart'
import RwLandingPage from '@/pages/RwLandingPage'
import RwRegister from '@/pages/RwRegister'

export default new VueRouter({
    routes: [
        //home page
        {
            path: '/plans',
            name: 'RwHome',
            component: RwHome,
        },

        //checkout page
        {
            path: '/checkout',
            name: 'RwCheckout',
            component: RwCheckout,
        },

        //success page
        {
            path: '/success',
            name: 'RwSuccess',
            component: RwSuccess,
        },

        //dashboard page
        {
            path: '/dashboard',
            name: 'RwDashboard',
            component: RwDashboard,
        },

        //login page
        {
            path: '/login',
            name: 'RwLogin',
            component: RwLogin,
        },

        //test page
        {
            path: '/test',
            name: 'RwTest',
            component: RwTest,
        },

        // cart page
        {
            path: '/cart',
            name: 'RwCart',
            component: RwCart,
        },

        // register page
        {
            path: '/register',
            name: 'RwRegister',
            component: RwRegister,
        },

        // landing page
        {
            path: '/',
            name: 'RwLandingPage',
            component: RwLandingPage,
        },
    ],
})
