<template>
    <div>
        <v-container v-if="cart.length !== 0" fluid>
            <v-row align="center" justify="center" class="mt-10">
                <v-stepper v-model="e6" vertical elevation="0">
                    <v-stepper-step editable :complete="e6 > 1" step="1" color="primary"> Buy </v-stepper-step>
                    <v-stepper-content step="1">
                        <v-row no-gutters>
                            <v-col v-for="(item, index) in cart" :key="`cart-${index}`" cols="8">
                                <div class="subheading">{{ item.data.name }} * {{ item.quantity }}</div>
                            </v-col>
                        </v-row>

                        <br />
                        <v-btn color="primary" @click="e6 = 2" class="mr-4 mb-2 white--text"> Continue </v-btn>

                        <router-link style="text-decoration: none; color: inherit" to="/cart">
                            <v-btn class="mb-2" text> Cancel </v-btn>
                        </router-link>
                    </v-stepper-content>

                    <v-stepper-step :rules="[() => accountRules]" editable :complete="e6 > 2" step="2" color="primary">
                        Create Account
                        <small v-if="!accountRules">{{ accountErrorMessage }}</small>
                    </v-stepper-step>
                    <v-stepper-content step="2">
                        <AddAccount />
                    </v-stepper-content>

                    <v-stepper-step editable :complete="e6 > 3" step="3" color="primary"> Address </v-stepper-step>
                    <v-stepper-content step="3">
                        <AddAddress />
                    </v-stepper-content>

                    <v-stepper-step :rules="[() => numberRules]" editable :complete="e6 > 4" step="4" color="primary">
                        Number Selection
                        <small v-if="!numberRules">{{ numberErrorMessage }}</small>
                    </v-stepper-step>
                    <v-stepper-content step="4">
                        <NumberSelection />
                    </v-stepper-content>

                    <v-stepper-step editable :complete="e6 > 5" step="5" color="primary"> Payment </v-stepper-step>
                    <v-stepper-content step="5">
                        <AddPayment />
                    </v-stepper-content>

                    <v-stepper-step editable :complete="e6 > 6" step="6" color="primary">
                        Review & Submit Order
                    </v-stepper-step>
                    <v-stepper-content step="6">
                        <OrderReview />
                    </v-stepper-content>
                </v-stepper>
            </v-row>
        </v-container>
        <!-- let user know come back to last page -->
        <v-container v-else fluid>
            <v-row align="center" justify="center" class="mt-10">
                <v-card class="mx-auto" max-width="500" elevation="0">
                    <v-card-title class="justify-center">
                        <p class="text-h4 font-weight-bold">Please select a plan</p>
                    </v-card-title>
                    <v-card-text>
                        <router-link style="text-decoration: none; color: inherit" to="/plans">
                            <v-btn class="mb-2" text> Go to Plans </v-btn>
                        </router-link>
                    </v-card-text>
                </v-card>
            </v-row>
        </v-container>
    </div>
</template>

<script>
    import AddAccount from './AddAccount'
    import AddAddress from './AddAddress'
    import NumberSelection from './NumberSelection'
    import AddPayment from './AddPayment'
    import OrderReview from './OrderReview'
    import { mapState, mapActions } from 'vuex'
    export default {
        name: 'CheckoutSteppers',
        components: {
            AddAccount,
            AddAddress,
            AddPayment,
            OrderReview,
            NumberSelection,
        },
        data() {
            return {
                cart: [],
                // planInfo: {},
                // accountError: 200,
            }
        },
        methods: {
            ...mapActions('checkout', ['setStepper', 'setPlanInfo']),
        },
        computed: {
            ...mapState('checkout', {
                accountError: (state) => state.accountError,
                numberError: (state) => state.numberError,
                planInfo: (state) => state.planInfo,
            }),
            accountRules() {
                return this.accountError === 200
            },
            numberRules() {
                return this.numberError === 200
            },
            accountErrorMessage() {
                switch (this.accountError) {
                    case 409:
                        return 'Account already exists'
                    case 400:
                        return 'Invalid account information'
                    default:
                        return 'Unknown error'
                }
            },
            numberErrorMessage() {
                switch (this.numberError) {
                    case 409:
                        return 'Number already exists'
                    case 400:
                        return 'Invalid number information'
                    case 500:
                        return 'Internal server error'
                    default:
                        return 'Unknown error'
                }
            },
            e6: {
                get() {
                    console.log('get', this.$store.state.checkout.stepper)
                    return this.$store.state.checkout.stepper
                },
                set(step) {
                    this.setStepper(step)
                },
            },
        },
        mounted() {
            // this.planInfo = this.$route.query
            console.log('@checkout: ', this.planInfo)
            // read cart from session storage
            this.cart = JSON.parse(sessionStorage.getItem('cart'))
            console.log('cart: ', Object.keys(this.cart))
            // console.log('@@@this.planInfo', this.planInfo)
            // this.setPlanInfo(this.planInfo)
        },
        beforeDestroy() {},
    }
</script>

<style scoped></style>
