<template>
    <div v-if="hasPlan">
        <!-- <div class="text-h6 active mb-0 transition-swing" v-text="text"></div> -->
        <v-select
            :items="items"
            label="Numbers"
            v-model="numberData.number"
            v-if="getNumberClicked === true"
        ></v-select>
        <v-btn class="mr-4 mb-4 white--text" color="primary" @click="getNumber" :disabled="getNumberClicked">
            Get number
        </v-btn>
        <v-btn class="mr-0 mb-4 white--text" color="primary" @click="submit" :disabled="!getNumberClicked">
            continue
        </v-btn>
    </div>
    <div v-else>
        no need to select number
        <br />
        <v-btn class="my-4 white--text" color="primary" @click="submit"> continue </v-btn>
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    export default {
        name: 'NumberSelection',
        data() {
            return {
                // text: 'Number Selection',
                items: [
                    { text: this.randomPhoneNumber() },
                    { text: this.randomPhoneNumber() },
                    { text: this.randomPhoneNumber() },
                ],
                getNumberClicked: false,
                numberData: {
                    number: '',
                },
            }
        },
        methods: {
            ...mapActions('checkout', ['setNumber', 'setStepper']),
            getNumber() {
                this.getNumberClicked = true
            },
            submit() {
                // store the number in the vuex
                this.setNumber(this.numberData)
                // go to the next step
                this.setStepper(5)
            },
            // random U.S. phone number generator
            randomPhoneNumber() {
                let phoneNumber = ''
                for (let i = 0; i < 10; i++) {
                    phoneNumber += Math.floor(Math.random() * 10)
                }
                return phoneNumber
            },
            hasPlan() {
                // !planInfo.data.categories.includes('ef6bebd166824c43b802400522e1e150')
                // traverse the cart and check if there is a plan
                for (let i = 0; i < this.cart.length; i++) {
                    if (this.cart[i].data.categories.includes('ef6bebd166824c43b802400522e1e150')) {
                        return true
                    }
                }
                return false
            },
        },
        computed: {
            ...mapState('checkout', ['planInfo']),
        },
    }
</script>

<style scoped></style>
