<template>
    <div>
        <v-container v-if="cart.length > 0">
            <div class="text-h4 text-center my-10">Your Cart</div>
            <v-row align="center" no-gutters style="height: 50px">
                <v-col cols="8">
                    <v-card class="pa-2 text-h6" elevation="0"> Item </v-card>
                </v-col>
                <v-col cols="2">
                    <v-card class="pa-2 text-h6" elevation="0"> Quantity </v-card>
                </v-col>
                <v-col cols="2">
                    <v-card class="pa-2 text-h6" elevation="0"> Price </v-card>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-row v-for="(item, index) in cart" :key="`item-${index}`" align="center" no-gutters style="height: 50px">
                <v-col cols="8">
                    <v-card class="pa-2 text-body-2" elevation="0"> {{ item.data.name }} </v-card>
                </v-col>
                <v-col cols="2">
                    <!-- edit quantity-->
                    <v-card class="pa-2 text-body-2" elevation="0">
                        <v-row>
                            <v-btn @click="decrementQuantity(index)" icon>
                                <v-icon>mdi-minus</v-icon>
                            </v-btn>
                            <v-card class="pa-2 text-body-2" elevation="0"> {{ item.quantity }} </v-card>
                            <v-btn @click="incrementQuantity(index)" icon>
                                <v-icon>mdi-plus</v-icon>
                            </v-btn>
                        </v-row>
                    </v-card>
                </v-col>
                <v-col cols="2">
                    <v-card class="pa-2 text-body-2" elevation="0">
                        $ {{ item.data.amounts.primary * item.quantity }}
                        <v-btn small icon @click="removeItem(item.id)">
                            <v-icon>mdi-delete</v-icon>
                        </v-btn>
                    </v-card>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-row align="center" no-gutters style="height: 50px">
                <v-col cols="10">
                    <v-card class="pa-2 text-h6" elevation="0"> Subtotal </v-card>
                </v-col>
                <v-col cols="2">
                    <v-card class="pa-2 text-h6" elevation="0"> $ {{ total }} </v-card>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-row align="center" justify="center" class="mt-10">
                <router-link style="text-decoration: none; color: inherit" to="/plans">
                    <v-btn class="mb-2 mr-4"> Continue Shopping </v-btn>
                </router-link>
                <v-btn color="primary" @click="checkout" class="mr-4 mb-2 white--text"> Checkout </v-btn>
            </v-row>
        </v-container>
        <v-container v-else>
            <div class="text-h3 text-center my-10">Your Cart is Empty</div>
            <v-row align="center" justify="center" class="mt-10">
                <router-link style="text-decoration: none; color: inherit" to="/plans">
                    <v-btn class="mb-2"> Continue Shopping </v-btn>
                </router-link>
            </v-row>
        </v-container>
    </div>
</template>

<script>
    export default {
        name: 'CartPage',
        data() {
            return {
                cart: [],
            }
        },
        computed: {
            total() {
                let total = 0
                this.cart.forEach((item) => {
                    total += Number(item.data.amounts.primary) * item.quantity
                })
                return total.toFixed(2)
            },
        },
        methods: {
            checkout() {
                this.$router.push('/checkout')
                // save cart to store
                // this.$store.commit('cart/setCart', this.cart)
            },
            removeItem(id) {
                this.cart = this.cart.filter((item) => item.id !== id)
                // update cart in session storage
                sessionStorage.setItem('cart', JSON.stringify(this.cart))
            },
            incrementQuantity(index) {
                this.cart[index].quantity += 1
                // update cart in session storage
                sessionStorage.setItem('cart', JSON.stringify(this.cart))
            },
            decrementQuantity(index) {
                if (this.cart[index].quantity > 1) {
                    this.cart[index].quantity -= 1
                    // update cart in session storage
                    sessionStorage.setItem('cart', JSON.stringify(this.cart))
                }
            },
        },
        mounted() {
            this.cart = JSON.parse(sessionStorage.getItem('cart'))
            console.log('@cart: ', this.cart)
        },
    }
</script>

<style scoped></style>
